.bl-form {
    display: grid;
    grid-template-areas: "a b c"
        "d d d"
        "e e e"
        "e e e"
        "f f f";
    grid-column-gap: 2vw;
    -webkit-column-gap: 2vw;
            column-gap: 2vw;
    grid-row-gap: 1vw;
    row-gap: 1vw;
}

.input-div {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 5vw;
    justify-content: space-between;
}

.top-input {
    height: 3vw;
    width: 17vw;
    border-radius: 5px;
    font-size: 1.5vw;
}

#nameDiv {
    grid-area: a;
}

#phoneDiv {
    grid-area: b;
}

#mailDiv {
    grid-area: c;
}

#subjectDiv {
    grid-area: d;
}

#subjectDiv input {
    width: 56vw;
}

#contentDiv {
    grid-area: e;
    height: 17.5vw;
}

.content-input {
    width: 56vw;
    height: 15vw;
    resize: none;
    border-width: 2px;
    border-style: inset;
    border-color: -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
    border-image: none;
    border-image: initial;
}

#submitDiv {
    grid-area: f;
    position: relative;
}

